.App {
  font-family: Arial, Helvetica, sans-serif;

  &__start {
    display: block;
    margin: 200px auto;
  }

  &__clear-button {
    margin-top: 25px;
  }

  &__list {
    list-style-type: none;
    margin: 100px auto 0;
    width: 350px;
  }

  &__list-item {
    margin: 10px;
    display: flex;
    justify-content: space-between;

    &--active {
      background-color: yellow;
    }
  }

  &__buttons {
    width: 400px;
    margin: 100px auto 0;

    > button {
      margin-right: 5px;

      &:focus {
        background-color: yellow;
      }
    }
  }
}
